.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  padding-bottom: 24px;
  overflow: hidden;
  box-shadow: 0 2px 5px #0000001a;
}

.card__image {
  object-fit: cover;
  background: #fff;
  border-radius: 5px;
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
  display: block;
}

.card__title {
  color: #060b35;
  letter-spacing: 0;
  text-align: left;
  margin: 40px 16px 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  margin: 4px 16px 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  align-items: center;
  margin: 16px 16px 0;
  display: flex;
}

.card__stars {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.card__reviews {
  letter-spacing: 0%;
  text-align: right;
  vertical-align: bottom;
  margin-left: 17px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-block {
  justify-content: space-between;
  align-items: center;
  margin: 24px 16px 0;
  display: flex;
}

.card__price-label {
  color: #616070;
  justify-content: flex-start;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  justify-content: flex-end;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px 16px 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: background-color .3s, color .3s, border-color .3s;
  display: inline-block;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  padding: 2px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 0;
  line-height: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  flex: 0 0 13px;
  width: 13px;
  height: 12px;
  display: block;
}

.stars__star:first-child {
  margin-left: 2px;
}

.stars__star + .stars__star {
  margin-left: 7px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  padding: 0;
}

.container {
  background: #f5f5f5;
  justify-content: center;
  align-items: flex-start;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
}
